:local .item {
    width: 50%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 0;
    padding-bottom: 4px;
}

:local .item:nth-child(even) {
    padding-left: 2px;
    padding-right: 4px;
}

:local .item:nth-child(odd) {
    padding-left: 4px;
    padding-right: 2px;
}

:local .item:nth-child(1), :local .item:nth-child(2) {
    padding-top: 4px;
}

:local .text {
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
}


