:local .more {
    z-index: 1;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.6);
}

:local .show {
    visibility: visible;
    opacity: 1;
    -webkit-transition: all .1s linear;
    -o-transition: all .1s linear;
    transition: all .1s linear;
}

:local .hidden {
    visibility: hidden;
    opacity: 0;
    -webkit-transition: all .1s linear .05s;
    -o-transition: all .1s linear .05s;
    transition: all .1s linear .05s;
}

:local .btn-show {
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    -webkit-transition: all .1s cubic-bezier(1, 2.23, 1, .84) .1s;
    -o-transition: all .1s cubic-bezier(1, 2.23, 1, .84) .1s;
    transition: all .1s cubic-bezier(1, 2.23, 1, .84) .1s;
}

:local .btn-hidden {
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
    -webkit-transition: all .1s linear;
    -o-transition: all .1s linear;
    transition: all .1s linear;
}

:local .icon {
    width: 30px;
}

:local .text {
    margin-left: 30px;
    color: #707070;
    font-size: 12px;
}

:local .active {
    background-color: #26262b;
    border-left: 5px solid #fff;
}


